.app{
  width: 100%;
  margin: 100px auto;
  box-shadow: 0 0 5px #ccc;
}
.details{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 30px 0;
}
.details .big-img{
  max-width: 700px;
  min-width: 290px;
  overflow: hidden;
  margin: 25px;
}
.big-img img{
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}

.details .big-img2{
  display: none;
}
.big-img2 img{
  display: none;
}

.video1 {
  margin: 25px;
}

.video2 {
  margin: 25px;
  display: none;
}

.details .box{
  max-width: 700px;
  min-width: 290px;
  margin: 25px;
}
.box .row{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 15px;
}
.box .row h2{
  text-transform: uppercase;
  letter-spacing: 2px;
}
.box .row div .price{
  color: #83ac2b;
  font-size: 20px;
}
.box .row div .discount{
  color: #d2c1bd;
  margin-right: 10px;
  font-size: 20px;
  text-decoration: line-through;
}

.box .colors button{
  width: 30px;
  height: 30px;
  border: 1px solid #333;
  outline: none;
  margin-right: 5px;
  cursor: pointer;
}
.box p{
  line-height: 1.5;
  margin: 15px 0;
}
.thumb{
  width: 100%;
  height: 100px;
  display: flex;
  cursor: pointer;
  margin: 10px 0;
}
.thumb img{
  width: 90px;
  height: 100%;
  display: block;
  object-fit: cover;
  border: 1px solid #ddd;
  margin-right: 5px;
  opacity: 0.7;
  border-radius: 5px;
}
.thumb img.active{
  opacity: 1;
  border: 1px solid lightseagreen;
}
.box .cart{
  background: #333;
  color: white;
  outline: none;
  border: none;
  cursor: pointer;
  padding: 10px 25px;
  margin-top: 15px;
}

@media screen and (max-width: 1000px){
  .thumb{
    height: 50px;
    justify-content: center;
  }
  .thumb img{
    width: 50px;
  }
  .video1 {
    display: none;
  }
  .video2 {
    margin: 0px;
    display: block;
    width: 100%;
    height: 100%;
  }
  .details .big-img2{
    display: block;
    max-width: 700px;
    min-width: 290px;
    overflow: hidden;
  }
  .big-img2 img{
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
  }
}